import React, { useEffect,useState } from "react";
import ReactDOM from "react-dom";
import { CSSTransition } from "react-transition-group";
import "./Modal.css";

import axios from "axios";


const ModalBroker = (props) => {
  
  const [modaldata, setmodaldata] = useState(props.filteredData);
  const usersString = localStorage.getItem("users");
  const users = JSON.parse(usersString);
const newModal = modaldata.filter((item) =>  item.brokers ===users.link_id);
  const API_URL = process.env.REACT_APP_SERVER_URL;
  const handleSoldstatus =async(el,status)=>{
    const id = el.id;
    const value=status;
    
    await axios
    .put(`${API_URL}/updatesold`, {
      id: id,
      status:value
    })
    .then((response) => {

      axios
      .get(`${API_URL}/catalougedata`)
      .then((response) => {
        
    
    setmodaldata(response.data)
    // console.log(modaldata);
     
      })
      .catch((error) => {
       
        console.log(error);
      });
    
    })
    .catch((error) => {
      console.log(error);
    });
    
    }
  const closeOnEscapeKeyDown = e => {
    if ((e.charCode || e.keyCode) === 27) {
      props.onClose();
    }
  };

  useEffect(() => {
    axios
    .get(`${API_URL}/catalougedata`)
    .then((response) => {
  // console.log(7899);
  setmodaldata(response.data)
   
    })
    .catch((error) => {
     
      console.log(error);
    });
    // setmodaldata(props.filteredData)
  
    document.body.addEventListener("keydown", closeOnEscapeKeyDown);
    return function cleanup() {
      document.body.removeEventListener("keydown", closeOnEscapeKeyDown);
    };
  }, []);

return(
    <>
    <CSSTransition
      in={props.show}
      unmountOnExit
      timeout={{ enter: 0, exit: 300 }}
    >
      <div className="modal" onClick={props.onClose}>
        <div className="modal-content" onClick={e => e.stopPropagation()}>
          <div className="">
            <h4 className="modal-title">{props.title}</h4>
<div className="table-responsive modal-custom">
  

            <table class="table table-bordered">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Lot</th>
              <th scope="col">Factory</th>
              <th scope="col">Price</th>
              <th scope="col">Bidder</th>
              <th scope="col">Status</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody >
            {newModal.map((el, index) => {
              // console.log(props.filteredData);
              return (
                <tr>
                  <td scope="row">{index + 1}</td>
                  <td>{el.lot}</td>
                  <td>{el.factory_name}</td>
                  <td>{el.maximum_bid}</td>
                  <td>{el.buyer_name}</td>
                  
                  <td>
                  <span className={el.sold_status === 1 ? "hname" : el.sold_status === 2 ? "rname" : "rname"}>
                      {el.sold_status === 1 ? "Sold" :  el.sold_status === 2 ?"Unsold": el.sold_status === 0 ?"No Bid":''}
                    </span>
                  </td>
                  <td>
                    <button type="" onClick={()=>handleSoldstatus(el,1)}>Sold</button>
                    <button type="" onClick={()=>handleSoldstatus(el,2)}>Unsold</button>
                    <button type="" onClick={()=>handleSoldstatus(el,0)}>Reauction</button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
        </div>

          </div>
          <div className="modal-body">{props.children}</div>
          <div className="modal-footer">
            <button onClick={props.onClose} className="btn btn-danger">
              Close
            </button>
          </div>
        </div>
      </div>
    </CSSTransition>,
  </>
  );
};

export default ModalBroker;
